.Modal {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	display: block;
	width: 100%;
	height: 100%;
	z-index: 1;
}

.Modal .center {
	width: 100%;
	height: 100%;
}

.ModalUnderlay {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	display: block;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,0.5);
	cursor: crosshair;
}

.ModalContent {
	position: absolute;
	background: white;
	border-radius: 1em;
	z-index: 1;
	overflow: hidden;
	-webkit-box-shadow: 0 0 0.5em rgba(0,0,0,0.5);
	        box-shadow: 0 0 0.5em rgba(0,0,0,0.5);
}

.Modal h1 {
	color: white;
	background: #333;
	margin: 0;
	padding: 0.5em 1em;
	text-align: left;
	font-size: inherit;
	overflow: hidden;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
	white-space: nowrap;
}

.Modal p {
	padding: 0.5em 1em;
}

.Modal input {
	display: block;
	width: 75%;
	max-width: 500px;
	padding: 0.5em;
	margin: 0 auto 0.5em;
	font: inherit;
	letter-spacing: inherit;
	border: none;
	-webkit-box-shadow: 0 0 0.25em rgba(0,0,0,0.25);
	        box-shadow: 0 0 0.25em rgba(0,0,0,0.25);
}

/* ModalButton */
.ModalButton {
	cursor: pointer;
	border: 1px solid #bbb;
	background: #eee;
	padding: 0.5em 0.5em;
	margin: 0;
	text-align: center;
	-ms-flex-preferred-size: 50%;
	    flex-basis: 50%;
}

.ModalButton:active {
	background: -webkit-gradient(linear, left top, left bottom, from(#eee), to(#fff));
	background: -webkit-linear-gradient(#eee, #fff);
	background: -o-linear-gradient(#eee, #fff);
	background: linear-gradient(#eee, #fff);
}

.ModalButton:disabled {
	cursor: default;
	background: #ddd;
}

.ModalButton img {
	width: 1.5em;
	height: 1.5em;
	vertical-align: middle;
	margin-right: 0.5em;
}

.ModalButton:disabled img {
	-webkit-filter: grayscale(100%);
	        filter: grayscale(100%);
}

/* ModalButtons */
.ModalButtons {
	display: -ms-flexbox;
	display: flex;
}



